.popup{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(20, 20, 20, 0.8);
    
    z-index: 100000;
    display: flex;
    justify-content: center;
    align-items: center;


}

.popup-inner{
    position: relative;
    padding: 32px;
    width: 100%;
    max-width: 640px;
    background-color: white;

}

.popup-inner .close-btn{
    position: absolute;
    top: 16px;
    right: 16px;
}