  color: white;
  /* height: 65vh; */
  background-size: cover;
  background-position: right;
}

#home{
  margin-right: 50%;
}

#home h1, h2, h3, button{
  text-align: left;
  padding: 2vh;
}

#home h1{
  font-size: 70px;

  font-weight: bolder;
  text-align: center;
}

#home h2 {
  font-size: 40px;
  padding-bottom: 30px;
}

#home h3{
  text-align: end;
  padding: 5%;

}

.titlehome{
  background-color: rgba(0, 0, 0, 0.7);
}

@media screen and (max-width:650px){
  #home h3, h2{
    display: none;
  }
  #home h1{
    font-size: 40px;
  }

  #home button{
    font-size: 20px;
    padding: 0 15px;
  }
  #homepic{
    height: 30vh;
  }

}

.titlealiarg:hover {
  color: white !important
}

.footer {
  background-color: #D7D6DE;
  width: 100%;
  box-shadow: 0 -5px 6px rgba(0,0,0,0.5);
  z-index: 10000;
}
.App {
  text-align: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.react-multi-carousel-item{
  display: flex;
  justify-content: center;
}

#carrousel{

  background-color: #D7D6DE;
  padding: 0 0 2vh 0;
  margin: 30px 20px;
  border-radius: 5px;
}

#titlenovedades{
  text-align: left;
  padding: 0 0 10px 20px;
  font-weight: bolder;
  font-size: 20px;
  font-family: roboto;
  color: rgb(36 24 65)
}

.footer{
  background-color: #D7D6DE;
}

.popup{
    position: fixed;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(20, 20, 20, 0.8);
    
    z-index: 100000;
    display: flex;
    justify-content: center;
    align-items: center;


}

.popup-inner{
    position: relative;
    padding: 32px;
    width: 100%;
    max-width: 640px;
    background-color: white;

}

.popup-inner .close-btn{
    position: absolute;
    top: 16px;
    right: 16px;
}
